
* {
  box-sizing: border-box; /*includes borders in box sizes */
}

@import '_variables'; /*colour key*/


body { /*base for site*/
  font-size: 1rem;
  font-family: 'Poppins', sans-serif;
  letter-spacing: .4px;
  color: #666;
  line-height: 1.42857143;
  background-color: rgba(0,0,0,.03);
}


 .header-container {
  display: flex;
  flex-direction: column;
  padding: 20px 15px;
  max-width: 100%;
  align-items: center;
  background-color: #FFFFFF;

  .top { /*top half of the header */
    display: flex;
    flex-direction: row;
    max-width: 100%;
    margin-bottom: 25px;
    .logo {
      object-fit: contain;
      max-width: 100%;
      flex-shrink: 1;
      margin-top: 10px;
      img {
        max-width: 100%;
    }
  }
 }
 }

 .phone { /*phone icon in header, below class creats underline */
   font-size: 30px;
   color: #333645;
   margin-left: 20px;
   margin-right: 30px;
   margin-bottom: 20px;
   }

.phone:hover {
  border-bottom: 3px solid #333645;
}

.header-container-bottom { /*lower half of the header */
  flex-direction: row;
  width: 100%;
  max-height: 5%;
  display: flex;
  flex-flow: row wrap;
  align-items: stretch;
   form {
     flex-grow: 1;
   }
}
.search-size {
  width: 100%;
  height: 42px;
  border-right: none;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  float: left;
  background-color: #d6d6d6;
  border-color: #999aa2;
}

.header-search-button {
  height: 42px;
  width: 42px;
  color: #333645;
  font-size: 1.5rem;
  border-left: none;
  border-color: #ccc;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  background-color: #d6d6d6;

}


.header-buttons {
  border-radius: 5px;
  padding: 5 15;
  font-size: 1.15rem;
  color: #FFFFFF;
  width: 9rem;
  height: 3.5rem;
}

.header-support {
  background-color: #4183d7;
  border: none;
}

.header-contact {
  background-color: #999aa2;
  border: none;
  }


.small {
  font-size: 0.5rem;
}
.navbar {
  display: flex;
  justify-content: center !important;
  background-color: #333645;
  padding: 0px !important;
}

.navbar-box {
  list-style-type: none;
  background-color: #333645;
  color: #FFFFFF;
  padding-top: 10px;
  padding-right: 60px;
  padding-left: 60px;
  padding-bottom: 10px;
  text-align: center;
  margin-bottom: 0px;
}


.hero { /*hero banner */
  background: url("../bio-image/bio-web.jpg") no-repeat center;
  background-size: cover;
  height: 740px;
}

 .hero-text {
  text-align: center;
  max-width: 100%;
  color: #FFFFFF;
  padding: 300px 15px;
   h1 {
     font-size: 48px;
     font-weight: 400;
   }
   p {
     font-size: 20px;
     font-weight: inherit;
   }
}
@mixin hero-button { /*pointless mixin for hero button, was used elsewhere but deemed redundant */
    color: #FFFFFF;
    background-color: #C8A2C8;
    padding: 5px 20px;
    border-radius: 5px;
    border-style: none;
    font-size: 20px;
    line-height: 50px;
}

.hero-button {
  @include hero-button
}



.bio { /*netmatters bio further down the page */
  max-width: 100%;
  color: #FFFFFF;
  background-color: #333645;
  padding: 40px 15px;
  line-height: 2;
}



.bio-button {
  background-color: #fff;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  color: #333645;
}

.latest-master { /*anything beginning with .latest is for the little bit that literally just says 'latest' with a bar below it */
  display: flex;
  flex-direction: row;
  background-color: #FFFFFF;
}
.latest-container {
  display: flex;
  flex-direction: column;
  padding-left: 10px;
}

.latest-header {
 padding-top: 30px;
 color: #000000;
 font-size: 20px;
 font-weight: 600;;
 line-height: 24px;
}

.latest-bar {
  margin-top: 30px;
  height: 5px;
  max-width: 100%;
  background-color: #000000;
}


.cards-container { /*7 cards for services netmatters offers */
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin-top: 40px;
  background-color: rgba(255, 255, 255, 0.1);
  padding-top: 20px;
}

@mixin circle { /*mixin to create the little circle on the cards */
  height: 60px;
  width: 60px;
  border-radius: 50%;
  margin-left: 40%;
  margin-right: 40%;
  color: #FFFFFF;
  font-size: 1.1rem;
  margin-bottom: 1rem;
  justify-content: center;
} /*background circle on cards */

.movedown { /*moves the circle into the right place on Y axis */
  line-height: 3.5rem;
}

.circle-bespoke { /*7x classes to change background of each circle */
  @include circle;
  background-color: $slate;
}

.circle-it {
  @include circle;
  background-color: $blue;
}

.circle-digital {
  @include circle;
  background-color: $green;
}

.circle-telecoms {
  @include circle;
  background-color: $red;
}

.circle-web {
  @include circle;
  background-color: $purple;
}

.circle-cyber {
  @include circle;
  background-color: $pink;
}

.circle-dev {
  @include circle;
  background-color: $purple;
}


@mixin cards { /* creates the cards */
  text-align: center;
  max-height: none;
  max-width: 100%;
  border-radius: 5px;
  border: 1px solid #ccc;
  justify-content: center;
  padding: 30px;
  margin-left: 10px;
  margin-right: 10px;
  margin-bottom: 35px;
  background-color: #ffffff;
  box-shadow: 0 10px 6px -6px;
}


.cards { /*typography adjustments for the 7 cards */
  @include cards;
  color: #000000;
  h2 {
    font-size: 18px;
    font-weight: 400px;
  }
  p {
    font-size: 13px;
    line-height: 22px;
  }
}

.cards-bar { /*black bar for cards */
  height: 3px;
  width: 30px;
  background-color: #000000;
  margin-left: 45%;
  margin-right: 45%;
  margin-bottom: 20px;
  margin-top: 20px;
}

@mixin cards-button { /*button without colour for cards, below is each background colour */
  color: #FFFFFF;
  padding: 10px;
  border-radius: 5px;
  border-style: none;
}

.cards-button-bespoke {
  @include cards-button;
  background-color: $slate;
}

.cards-button-it {
  @include cards-button;
  background-color: $blue;
}

.cards-button-digital {
  @include cards-button;
  background-color: $green;
}

.cards-button-telecoms {
  @include cards-button;
  background-color: $red;
}

.cards-button-web {
  @include cards-button;
  background-color: $purple;
}

.cards-button-cyber {
  @include cards-button;
  background-color: $pink;
}

.cards-button-dev {
  @include cards-button;
  background-color: $purple;
}




.news {
  max-width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 10px;
  margin-right: 10px;
  padding-top: 30px;
  padding-bottom: 10px;
}



@mixin news-cards { /*creates 3x news cards */
  text-align: left;
  height: 65%;
  max-width: 100%;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  border: 1px solid #ccc;
  justify-content: center;
  padding: 30px;
  margin-bottom: 30px;
  border-top: none;
}

.news-cards { /*typography for news cards subheading & button */
    @include news-cards;
  p:nth-child(2) {
    font-size: 13px;
  }
  button {
    padding: 5px 15px;
    color: #FFFFFF;
    background-color: #C8A2C8;
    border-radius: 5px;
    border-style: none;
    font-size: 17px;
  }
}

.news-cards .title {
  color: $purple;
}

.card-image { /*image that sits atop the cards */
  object-fit: contain;
  max-width: 100%;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}

.card-head .card-head-button { /*button for top right of card */
  background-color: $purple;
  right: 100%;
  color: #FFFFFF;
  font-size: 0.8rem;
  border: none;
  padding: 3px 10px;
  z-index: 1;
  position: absolute;
  transform: translate(313px, 10px);
}

.card-head {
 position: relative;
}


.grey-bar { /*grey bar above news card dates */
  max-width: 100%;
  background-color: #ccc;
  height: 1px;
  margin-top: 30px;
  margin-bottom: 30px;
}

.logo-date { /* logo and date stuff */
  display: flex;
  flex-direction: row;
  justify-content: center;
  float: left;
  font-size: 0.9rem;
  max-height: 47px;
}

.logo-date .date {
  display: flex;
  flex-direction: column;
  p:first-of-type {
    color: #333645;
  }
}

.small-logo {
  position: relative;
  height: 47px;
  width: 47px;
  margin-right: 30px;
}


.sponsors {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 50px 375px;
  background-image: linear-gradient(red, yellow);
}

.signup {
  color: #333645;
  padding: 50px 45px;
  background-color: #d6d6d6;
}

.signup-field {
  border-radius: 3px;
  border-color: rgba(85, 85, 85, 0);
  margin-bottom: 15px;
  width: 100%;
  height: 45px;
}



.checkbox { /*typography for checkbox container */
  display: flex;
  cursor: pointer;
  flex-direction: row;
  p {
    font-weight: 600px;
    line-height: 32px;
  a {
    color:inherit;
    text-decoration: underline;
    }
  }
}

.checkbox-input { /*hides original checkbox */
  display: none;
}

.checkbox-box { /*creates custom checkbox */
  height: 30px;
  width: 30px;
  border-radius: 5px;
  border: 3px solid #333645;
  display: flex;
  flex-shrink: 0;
  margin-right: 15px;
}


.checkbox-input:checked + .checkbox-box:after { /*creates a white tick */
  content: '\2714';
  color: #fff;
}

.checkbox-input:checked + .checkbox-box { /*highlights area in box */
  background-color: #333645;
  color: #ffffff;
  justify-content: center;
}



.subscribe-button { /*signup subscribe button*/
  background-color: #333645;
  padding: 10px 15px;
  color: #FFFFFF;
  border: none;
  border-radius: 5px;
}


/*footer*/
 footer { /*typography */
   h4 {
     font-size: 16px;
     line-height: 17.6px;
   }
   li {
     font-size: 13px;
     line-height: 22px;
   }
 }

.footer-container {
  background-color: #333645;
  color: #FFFFFF;
  display: flex;
  flex-direction: column;
  max-width: 100%;
  max-height: 100%;
  padding: 10% 5%;
}

.info-block ul { /*formatting for everything but the socials icons*/
  padding: 0%;
}

.info-box {
  list-style-type: none;
}

.info-block{
  display: flex;
  flex-direction: column;
  margin-bottom: 15px;
}

.socials-block {
  background-color: #333645;
  color: #FFFFFF;
  display: flex;
  flex-wrap: wrap;
  max-width: 100%;
  max-height: 100%;
}

.socials-block ul {
  margin: 0%;
  padding: 0%;
  display: flex;
  flex-wrap: wrap;
}


.socials { /*socials icons*/
  padding: 0.8rem 4rem;
  color: #FFFFFF;
  border: none;
  background-color: rgba(0,0,0,.25);
  display: flex;
  width: 40%;
  margin: 15px;
  justify-content: center;
  font-size: 2rem;
}




.facebook:hover { /*hover colour changes*/
  background-color: #4267B2;
}

.twitter:hover {
  background-color: #1DA1F2;
}

.linkedin:hover {
  background-color: #2867b2;
}


/*partners*/

.partner-images {
  margin: 50px 375px;
}

.partner-images-1 {
  display: flex;
  justify-content: center;
  gap: 50px;
}

.partner-images-2 {
  display: flex;
  justify-content: space-between;
  gap: 50px;
}

.hide {
  display: none;
}
.hide:hover .overlay{
  display: block;
}



/* media queries */
/*hides sponsors & partners for mobile viewing */
@media screen and (min-width: 0px) and (max-width: 1024px) {
  .partner-images, .sponsors, .navbar, .navbar-box, .header-buttons, .header-support, .header-contact{
    display: none;
      }
    }
